
body{
  background:#fff;
}
.btn{
  text-align:center;
  line-height:60px;
  border-radius:60px;
  margin:5px 5px;
  font-size:30px;
  background:#233;
}
.zero{
    grid-column: 1/3;
}
.first{
  background:#999090;
}
.operator{
  background:#f80;
}
.scnd .operator{
  background:#080;
}
#calculator {
  background:#000;
  color:#fff;
  margin:10px auto;
  padding: 10px;
  font-size:30px;
font-family:Avenir,Sans;
display:grid;
grid-template-columns: repeat(4, 1fr);
grid-auto-rows: minmax(50px, auto);
}
.display {
  grid-column: 1 / 5;
  font-size:70px;
  color:#fff;
  text-shadow: #000 1px 1px 0, #000 -1px -1px 0, #000 -1px 0 0;
  text-align:right;
  overflow:hidden;
  padding:0 10px;
  background: #000;
}














